		//回复
		replyQuestion(param) {
			let reg = /^\d+(\.\d+)?$/;
			let hfnr = $('#summernoteT').summernote('code');
			if (!reg.test(this.gsValue) || this.gsValue > 8) {
				this.$alert('请输入正确工时且单次工时不能超过8小时,大于8小时请分多个回复!', '提示', {
					confirmButtonText: '确定',
					type: 'warning'
				});
				return;
			}

			if (hfnr == '<p><br></p>' || hfnr == '') {
				this.$alert('请输入回复内容', '提示', {
					confirmButtonText: '确定',
					type: 'warning'
				});
			} else {
				this.isHF = true;
				saveAnswer({
					//回复问题
					zbwid: this.wid,
					nr: hfnr,
					gs: this.gsValue,
					sfbg: this.sfbgValue,
					cnjsrq: this.cnjsrq || '',
					hflx: param == 'hf' ? 1 : 99,
					tktwr: this.tktwr
				}).then(({ data }) => {
					if (data.state == 'success') {
						this.isHF = false;
						this.gsValue = 0;
						this.$message({
							message: '回复成功',
							type: 'success'
						});
						this.queryAnswers(this.wid);
						this.queryProcess(this.wid);
						$('#summernoteT').summernote('code', '');
						clearTimeout(this.timer);
						localStorage.removeItem(this.wid);
					} else {
						this.isHF = false;
						this.$alert(data.msg, '提示', {
							confirmButtonText: '确定',
							type: 'error'
						});
					}
				});
			}
